import {NavLink} from "react-router-dom";
import './Navbar.css'

import React, {useState} from "react";
import {AllNavbarType} from "./NavbarContainer";
import {slide as Menu} from 'react-burger-menu'


function Navbar(props: AllNavbarType) {


    const [menuOpenState, setMenuOpenState] = useState(false)

    const checkActive = (isTrue?: boolean) => (!props.isTrue ? '' : 'active')

    const friends = props.names.map((post) => <div key={post.id} className='dot'>{post.name}</div>)

    const showSettings = (event: any) => {
        event.preventDefault();
    }
    const closeMenu = () => {
        setMenuOpenState(!menuOpenState)
    }

    const toggleMenu = () => {
        setMenuOpenState(!menuOpenState)
    }


    let styles = {

        bmOverlay: {
            background: 'none',

        },
        bmMenu: {
            background: 'none',
        },
        bmMenuWrap: {
            // position: 'relative',
            height: '100%'
        },
        reactBurgerMenuBtn: {
            width: '50px',
            height: '50px'
        },
    }


    return (
        <>

            <div className='burger_menu' onClick={() => toggleMenu()}><span></span></div>
            {/*<button onClick={() => toggleMenu()}/>*/}
            <Menu styles={styles} disableOverlayClick onStateChange={(state) => setMenuOpenState(state.isOpen)}
                  width={200}>

                <nav className='nav'>

                    <div className='item'>
                        <NavLink id="home" className='menu_item' onClick={() => closeMenu()}
                                 to='./profile'>Profile</NavLink>
                    </div>
                    <div className='item'>
                        <NavLink id="users" className='menu_item'
                                 onClick={() => closeMenu()} to='/users'>Users </NavLink>
                    </div>
                    <div className='item'>
                        <NavLink id="dialogs" className='menu_item' onClick={() => closeMenu()}
                                 to='/dialogs'>Messages </NavLink>
                    </div>

                    <div className='item'>
                        <NavLink id="news" className='menu_item' onClick={() => closeMenu()} to='/news'>News</NavLink>
                    </div>
                    <div className='item'>
                        <NavLink id="music" className='menu_item' onClick={() => closeMenu()}
                                 to='/music'>Music</NavLink>
                    </div>
                    <div className='item'>
                        <NavLink id="settings" className='menu_item' onClick={() => closeMenu()}
                                 to='/settings'>Settings</NavLink>
                    </div>

                    <div className='friends'> Friends</div>
                    <div>{friends}</div>

                </nav>
            </Menu>
        </>
    )
}

export default Navbar;



